export const fetchTypeOfSportItems = ( store ) => {
  const { dispatch } = store;
  dispatch('typeofsport/fetchItems');
};

export const fetchTypeOfSportFilteredItems = (store, filter_field, filter_id) => {
  const { dispatch } = store;
  dispatch('typeofsport/fetchFilteredItems', filter_field, filter_id);
};


export const selectTypeOfSportItems = ( store ) => {
  const { getters } = store;
  return getters['typeofsport/items']
}

export const selectTypeOfSportItemById = (store, id) => {
  const { getters } = store;
  return getters['typeofsport/itemsByKey'][id] || {};
}

export const removeTypeOfSportItem = ( store, id ) => {
  const { dispatch } = store;
  dispatch('typeofsport/removeItem', id);
}

export const addTypeOfSportItem = ( store, { TypeOfSport } ) => {
  const { dispatch } = store;
  dispatch('typeofsport/addItem', { TypeOfSport });
}

export const updateTypeOfSportItem = ( store, { id, TypeOfSport }) => {
  const { dispatch } = store;
  dispatch('typeofsport/updateItem', { id, TypeOfSport });
}
