<template>
  <div class="form">
    <div v-if="form.id" class="form__item">
      <div class="form__label">
        <label for="id">ID</label>
      </div>
      <input v-model="form.id" disabled class="form__input"  id="id" placeholder="id" type="text">
    </div>
    <div class="form__item">
      <div class="form__label">
        <label for="Name">ФИО</label>
      </div>
      <input v-model="form.Name" class="form__input"  id="Name" placeholder="ФИО" type="text">
    </div>
    <div class="form__item">
      <v-file
          :title="'Фотография'"
          :placeholder="'Фотография'"
          :name="'photo'"
          :value="form.photo"
          @changeFile="previewFiles"
          @clear="clearFile"
      />
    </div>
    <div class="form__item">
      <div class="form__label">
        <label for="TypeOfSport">Вид спорта</label>
      </div>
      <Select
          v-model="form.TYPE_OF_SPORT_ID"
          class="form__select"
          name="TypeOfSport" id="TypeOfSport"
          :selectID="form.TYPE_OF_SPORT_ID"
          :items="typeOfSportList"
      ></Select>
    </div>
    <div class="form__item">
      <div class="form__label">
        <label for="Biography">Биография</label>
      </div>
      <input @change="previewFiles" class="form__input"  id="Biography" placeholder="Биография" type="file">
    </div>
    <div class="form__item">
      <div class="form__label">
        <label for="Year">Год начала карьеры</label>
      </div>
      <input v-model="form.Year" class="form__input"  id="Year" placeholder="Год" type="date">
    </div>
    <div class="form__item">
      <Btn @click="onClick" :disabled="!isValid" class="info">Сохранить</Btn>
    </div>
  </div>
</template>

<script>
import { useStore } from 'vuex';
import { computed, reactive, onBeforeMount, watchEffect } from 'vue';
import { useRouter } from 'vue-router';

import {selectSportsmanItemById, fetchSportsmanItems} from '@/store/sportsmans/selectors';
import {selectTypeOfSportItems, fetchTypeOfSportItems} from '@/store/typeofsport/selectors'
import Btn from '@/components/Btn/Btn';
import Select from "@/components/Select/Select";
import VFile from "@/components/File/File";


export default {
  name: 'SportsmansForm',
  props: {
    id: { type: String, default: '' },
  },
  components: {
    VFile,
    Select,
    Btn,
  },
  setup(props, context) {
    const store = useStore();
    const router = useRouter();
    const form = reactive({
      id: '',
      Name: '',
      Photo: '',
      TYPE_OF_SPORT_ID: 1,
      Biography: '',
      Year: '',
    });

    const isValid = computed(() => {
      for (const [key, value] of Object.entries(form))
        if (key !== 'id' && !(value)) return false
      return true
    });

    const previewFiles = (e) => {form[`${e.target.id}`] = `/assets/photos/${e.target.files[0].name}`;}

    onBeforeMount(() => {
      fetchSportsmanItems(store);
      fetchTypeOfSportItems(store);
    });

    watchEffect(() => {
      const Sportsman = selectSportsmanItemById( store,  props.id );
      Object.keys(Sportsman).forEach(key => {
        form[key] = Sportsman[key]
      })
    });
    return {
      form,
      isValid,
      previewFiles,
      typeOfSportList: computed(() => selectTypeOfSportItems(store)),
      onClick: () => {
        context.emit('submit', form);
        router.push({ name: 'Sportsmans' })
      },
      clearFile: (e) => {
        form.photo = "";
      },
    }
  },
}
</script>
