export const fetchSportsmanItems = (store) => {
  const { dispatch } = store;
  dispatch('sportsmans/fetchItems');
};

export const fetchSportsmanFilteredItems = (store, filter_field, filter_id) => {
  const { dispatch } = store;
  dispatch('sportsman/fetchFilteredItems', filter_field, filter_id);
};

export const selectSportsmanItems = (store, type_id = '') => {
  const { getters } = store;
  return  !!(type_id) ? getters['sportsmans/itemsByType'][type_id] || {} : getters['sportsmans/items'];
}

export const removeSportsmanItem = (store, id) => {
  const { dispatch } = store;
  dispatch('sportsmans/removeItem', id);
}

export const addSportsmanItem = (store, sportsman) => {
  const { dispatch } = store;
  dispatch('sportsmans/addItem', sportsman);
}

export const updateSportsmanItem = (store, sportsman) => {
  const { dispatch } = store;
  dispatch('sportsmans/updateItem', sportsman);
}

export const selectSportsmanItemById = (store, id) => {
  const { getters } = store;
  return getters['sportsmans/itemsByKey'][id] || {};
}