<template>
  <select v-model="selected" class="select">
    <option v-for="item in items" :key="item.id" :value="item.id" >
      {{ item.TypeOfSport }}
    </option>
  </select>
</template>

<script>
import {ref} from "vue";

export default {
  name: 'Select',
  props: {
    items: Array,
    selectID: Number
  },
  setup (props, { emit }) {
    const selected = ref(props.selectID);
    return {
      selected,
    }
  }
}
</script>
