<template>
  <Layout :title="id ? 'Редактирование записи' : 'Создание записи'">
    <SportsmansForm @submit="onSubmit" :id="id"  />
  </Layout>
</template>

<script>
import { useStore } from 'vuex';

import { updateItem, addItem } from '@/store/sportsmans/selectors';
import Layout from '@/components/Layout/Layout';
import SportsmansForm from "@/components/SportsmansForm/SportsmansForm";

export default {
  name: 'SportsmansEdit',
  props: {
    id: String,
  },
  components: {
    Layout,
    SportsmansForm,
  },
  setup() {

    const store = useStore();

    return {
      onSubmit: ({ id, Name, Photo, TYPE_SPORT_ID, Biography, Year }) => id ?
          updateItem(store, { id, Name, Photo, TYPE_SPORT_ID, Biography, Year }) :
          addItem(store, { Name, Photo, TYPE_SPORT_ID, Biography, Year } ),

    }
  }

}
</script>

